.privacy-policy-container {
  background-color: #f9f9f9;
  padding: 40px 60px;
  max-width: 1200px;
  margin: 0 auto;
  font-family: "Poppins", sans-serif;
  color: #333;
  line-height: 1.6;
}

/* Header styling */
.privacy-policy-container h1 {
  font-size: 36px;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

.privacy-policy-container p {
  font-size: 16px;
  color: #555;
  margin-bottom: 20px;
}

/* Section titles */
.privacy-policy-container h2 {
  font-size: 28px;
  color: #444;
  margin-top: 30px;
  margin-bottom: 10px;
  border-bottom: 2px solid #ddd;
  padding-bottom: 5px;
}

/* Lists under each section */
.privacy-policy-container ul {
  list-style: none;
  padding-left: 0;
  margin-bottom: 20px;
}

.privacy-policy-container ul li {
  font-size: 16px;
  margin-bottom: 10px;
  color: #555;
}

/* Emphasis on important terms */
.privacy-policy-container strong {
  font-weight: bold;
  color: #222;
}

/* Styling for the contact information */
.privacy-policy-container p:last-child {
  font-size: 18px;
  font-weight: bold;
  color: #444;
  margin-top: 30px;
}

.privacy-policy-container p:last-child span {
  color: #2c8fc5;
  cursor: pointer;
}

/* dark theme */
body.dark-mode .privacy-policy-container {
  background-color: #000;
}
body.dark-mode .privacy-policy-container h1,
body.dark-mode .privacy-policy-container h2,
body.dark-mode .privacy-policy-container strong {
  color: #fff;
}
body.dark-mode .privacy-policy-container p,
body.dark-mode .privacy-policy-container ul li {
  color: #b9b8b8;
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
  .privacy-policy-container {
    padding: 20px;
    font-size: 14px;
  }

  .privacy-policy-container h1 {
    font-size: 28px;
  }

  .privacy-policy-container h2 {
    font-size: 24px;
  }

  .privacy-policy-container p,
  .privacy-policy-container ul li {
    font-size: 14px;
  }
}
