
.about-page {
    padding: 40px;
    font-family: "Poppins", sans-serif;
  }
  
  .about-header {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .about-header h1 {
    font-size: 36px;
    color: #333;
    margin: 0;
  }
  
  .about-content {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .about-section {
    margin-bottom: 30px;
  }
  
  .about-section h2 {
    font-size: 24px;
    color: #2c3e50;
    border-bottom: 2px solid #e74c3c;
    padding-bottom: 10px;
    margin-bottom: 15px;
  }
  
  .about-section p {
    font-size: 18px;
    line-height: 1.6;
    color: #555;
  }
  
  .about-section ul {
    list-style-type: disc;
    padding-left: 20px;
  }
  
  .about-section ul li {
    font-size: 18px;
    color: #555;
    margin-bottom: 8px;
  }
  
  /* dark theme */
  body.dark-mode .about-header h1,
  body.dark-mode .about-section h2 {
    color: #fff;
  }
  body.dark-mode .about-section p,
  body.dark-mode .about-section ul li {
    color: #d1d1d1;
  }